body {
  font-family: "pretendard";
  background-color: #f1f1f1;
  height: 90vh;
  padding: 50px;
}

.project-container {
  max-width: 1500px;
  min-width: 500px;
  min-height: 80vh;
  margin: auto;
  padding: 40px 40px 50px 40px;
  box-sizing: border-box;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-back {
  font-size: 25px;
  margin-bottom: 20px;
  align-items: baseline;
  :hover {
    cursor: pointer;
  }
}

.project-box {
  max-width: 600px;
  width: 100%;
}

.project-img {
  width: 100%;
  max-height: 500px;
  border-radius: 10px;
}

.title {
  font-size: 30px;
  font-weight: 700;
}

.description {
  font-size: 22px;
  color: #555555;
}
